import format from "date-fns/format";
import getDay from "date-fns/getDay";
import parse from "date-fns/parse";
import startOfWeek from "date-fns/startOfWeek";
import React, { useState } from "react";
import { Calendar, dateFnsLocalizer } from "react-big-calendar";
import "react-big-calendar/lib/css/react-big-calendar.css";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import "./App.css";
import Tnavbar from "./navbar";

const locales = {
    "en-US": require("date-fns/locale/en-US"),
};

const localizer = dateFnsLocalizer({
    format,
    parse,
    startOfWeek,
    getDay,
    locales,
});

const events = [
    {
        title: "Final Exam",
        allDay: true,
        start: new Date(2022, 8, 26),
        end: new Date(2022, 8, 31),
    },
];


function App() {
    const [newEvent, setNewEvent] = useState({ title: "", start: "", end: "" });
    const [allEvents, setAllEvents] = useState(events);

    function handleAddEvent() {
        setAllEvents([...allEvents, newEvent]);
    }

    return (
        <div className="App">
            <Tnavbar/>
            <div className="content">
              <Calendar localizer={localizer} events={allEvents} startAccessor="start" endAccessor="end" style={{ height: 500, width:1000,margin: "50px" }} />
              <div className="form">
                <h2 style={{color:'#fff',letterSpacing:1}}>Add New Event</h2>
                  <textarea className="text" type="text" placeholder="Add Title" value={newEvent.title} onChange={(e) => setNewEvent({ ...newEvent, title: e.target.value })} />
                  <DatePicker showTimeSelect closeOnScroll={true} dateFormat="dd/MM/yyyy h:mm aa" placeholderText="Start Date" style={{ marginRight: "10px"}} selected={newEvent.start} onChange={(start) => setNewEvent({ ...newEvent, start })} />
                  <DatePicker showTimeSelect closeOnScroll={true} dateFormat="dd/MM//yyyy h:mm aa" placeholderText="End Date" selected={newEvent.end} onChange={(end) => setNewEvent({ ...newEvent, end })} />
                  <button stlye={{ marginTop: "10px" }} onClick={handleAddEvent}>
                      Add Event
                  </button>
              </div>
              
            </div>
        </div>
    );
}

export default App;